<template>
  <div class="user-edit-container">
    <!-- Header 区域 -->
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" fixed />

    <!-- 用户资料 -->
    <van-cell-group class="action-card">
      <van-cell title="头像" is-link center>
        <template #default>
          <van-image round class="avatar" :src="userProfile.photo" @click="editPic" />
          <input type="file" ref="iptPicRef" v-show="false" accept="image/*" @change="iptPicChange" />
        </template>
      </van-cell>
      <van-cell title="名称" is-link :value="userProfile.name" @click="showEditUserNameDialog" />
      <van-cell title="生日" is-link :value="userProfile.birthday" @click="showEditUserBirthdayDialog" />
    </van-cell-group>

    <!-- 编辑用户姓名对话框 -->
    <van-dialog v-model="userNameShow" title="修改用户名" show-cancel-button :beforeClose="beforeCloseFn">
      <input type="text" class="input-username" v-fofo v-model="inputUserName" :style="{ height: '50%' }" />
    </van-dialog>

    <!-- 时间选择器 -->
    <van-popup v-model="datetimePickerShow" position="bottom" round>
      <van-datetime-picker v-model="currentDate" type="date" title="选择年月日" :min-date="minDate" :max-date="maxDate" @cancel="datetimePickerShow = false" @confirm="datetimePickerConfirmFn" />
    </van-popup>
  </div>
</template>

<script>
import { getUserProfileAPI, updateUserPicAPI, updateUserProfileAPI } from '@/api/api.js'
import { Notify } from 'vant'
import { formatDate } from '@/utils/date.js'
import { mapMutations } from 'vuex'
export default {
  name: 'UserInfoEdit',
  data() {
    return {
      // 用户个人资料
      userProfile: [],
      // // 获取到的新的头像url
      // newUserPicUrl: ''  这里不要新建容器来存储新地址，只需要把获取到的新地址赋值给用户数据中的photo属性就行
      // 控制修改用户姓名对话框的显示和隐藏
      userNameShow: false,
      // 用户修改的姓名
      inputUserName: '',

      // 时间选择器配置
      minDate: new Date(1920, 0, 1),
      maxDate: new Date(), // 最大时间不用写，会默认获取当前时间
      currentDate: new Date(), // 当前时间也不用写，默认获取用户生日
      // 控制时间选择器的显示与隐藏
      datetimePickerShow: false
    }
  },
  async created() {
    const res = await getUserProfileAPI()
    // console.log(res)
    this.userProfile = res.data.data
  },
  methods: {
    ...mapMutations(['SET_USERPHOTO', 'SET_USERNAME']),
    // 监听用户头像改变的事件
    // e是一个事件类型，e.target.files[0]就是用户上传的头像
    async iptPicChange(e) {
      // 问题：一旦用户点了取消也会监听到改变的事件，会传一个空文件进去，图片就没有了，因此，需要判断一下
      if (e.target.files.length === 0) return
      // console.log(e.target.files[0]) // 其实数组就是一种特殊的对象，索引就相当于它的key名
      // 创建一个formdata实例对象userPicObj
      const userPicObj = new FormData()
      // 向实例对象中追加上传的图片文件
      userPicObj.append('photo', e.target.files[0])
      // 调用上传头像的接口，将文件以对象的形式传到服务器
      const res = await updateUserPicAPI(userPicObj)
      // console.log(res)
      // this.newUserPicUrl = res.data.data.photo
      // 这里不要新建容器来存储新地址，只需要把获取到的新地址赋值给用户数据中的photo属性就行
      this.userProfile.photo = res.data.data.photo
      // 更新成功之后，将图片保存到vuex里
      this.SET_USERPHOTO(res.data.data.photo)
    },
    // 点击头像，打开文件选择器
    editPic() {
      // 用户点击的是图片，但是触发的却是文件输入框，用js来模拟标签的事件触发
      this.$refs.iptPicRef.click()
    },
    // 点击姓名，展示修改姓名的对话框
    showEditUserNameDialog() {
      this.userNameShow = true
      // this.inputUserName = this.userProfile.name  我觉得不放进去好一点
    },
    // 修改姓名对话框关闭前的回调函数
    async beforeCloseFn(action, done) {
      // 定义一个正则规则
      const reg = /^[a-zA-Z0-9\u4e00-\u9fa5]{1,7}$/
      // console.log(action)
      if (action === 'confirm') {
        // 点击了确定
        if (reg.test(this.inputUserName) === true) {
          // 关闭弹窗
          done()
          // 调用接口
          await updateUserProfileAPI({ name: this.inputUserName })
          // 回显到前端页面上
          // 由于组件缓存了，所以回显不能通过赋值的方式，而要通过vuex状态管理的方式
          this.userProfile.name = this.inputUserName
          this.SET_USERNAME(this.inputUserName)
        } else {
          // 校验失败，不允许关闭弹窗
          done(false)
          Notify({ type: 'warning', message: '用户名只能是1-7位中英数字组合' })
        }
      } else {
        // 点击了取消，就关闭弹窗
        done()
      }
    },
    // 点击生日，弹出时间选择器对话框
    showEditUserBirthdayDialog() {
      this.datetimePickerShow = true
      // 这里不能直接把this.userProfile.birthday传过去，因为格式对不上，人家要的是一个日期对象，不是字符串，所以要通过new Date()转成时间对象
      this.currentDate = new Date(this.userProfile.birthday)
      // console.log(this.currentDate)
    },
    // 点击事件选择器的确定按钮时的函数
    async datetimePickerConfirmFn() {
      await updateUserProfileAPI({
        // 这里的时间一定要转换成后台要求的时间格式，用dayjs插件可以搞定
        birthday: formatDate(this.currentDate)
      })
      this.datetimePickerShow = false
      // 回显
      this.userProfile.birthday = formatDate(this.currentDate)
    }
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 46px;
  .avatar {
    width: 50px;
    height: 50px;
  }
}
/deep/ .van-dialog__content {
  text-align: center;
  .input-username {
    text-align: center;
    border: none;
    outline: none;
    line-height: 40px;
  }
}
</style>
